/* App.css */

body {
  font-family: Arial, sans-serif;
  background-color: rgb(18,57,7);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.App {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 400px;
}

h1 {
  font-size: 24px;
  margin: 0 0 20px;
  color: #333;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  font-size: 16px;
  text-align: left;
  margin-top: 10px;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  margin: 10px 0;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

.todo-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.todo-card {
  background-color: #f8f8f8;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 15px;
  width: 100%;
  max-width: 250px;
  text-align: left;
}

h3 {
  font-size: 20px;
  margin: 0 0 10px;
  color: #007bff;
}

p {
  font-size: 16px;
  margin: 0 0 10px;
}

.todo-card button {
  background-color: #f44336;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  margin: 5px;
  padding: 8px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.todo-card button:hover {
  background-color: #d32f2f;
}
